h1, h2, h3, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  box-sizing: border-box;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 50px;
  list-style: none;
  display: flex;
}

.movie-card {
  width: 395px;
  margin-bottom: 20px;
  position: relative;
  box-shadow: 4px 9px 6px -4px rgba(0, 0, 0, .52);
}

.poster-movie-card {
  width: 100%;
}

.info-movie-card {
  z-index: 20;
  font-size: 20px;
  line-height: 1.2;
  position: absolute;
  bottom: 0;
}

.genre-movie-card, .title-movie-card {
  margin: 0;
}

.title-movie-card {
  text-transform: uppercase;
  color: #fff;
  margin-left: 20px;
  margin-right: 20px;
}

.overlay-movie-card {
  width: 100%;
  height: 100%;
  z-index: 10;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, #000 92.16%);
  position: absolute;
  top: 0;
  left: 0;
}

.wrapper-movie-card {
  justify-content: space-between;
  margin-left: 20px;
  margin-right: 20px;
  display: flex;
}

.genre-year-movie-card {
  color: #b7b7b7;
  justify-content: space-between;
  display: flex;
}

.category-divider {
  height: 100%;
  color: #b7b7b7;
  border-left: 1px solid #b7b7b7;
  margin: 0 10px;
  padding: 0 5px;
  display: inline-block;
}

